.popup__info-container {   
    width: 430px;
    border-radius: 10px;
    position: relative;
    margin: auto;
    background-color: #fff;
    padding-top: 60px;
    padding-bottom: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 700px){

    .popup__info-container  {
        width: 282px;
        margin: auto;
    }
}