.popup {
    width: 100%;
    height: 100%;
    opacity: 0;
    display: flex;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
    visibility: hidden;
    transition: visibility 0.6s, opacity 0.6s linear;
    pointer-events: none;
}
