.profile {
    max-width: 880px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 50px;
}

@media screen and (max-width: 930px){

    .profile {
        max-width: 282px;
        flex-direction: column;
        margin-bottom: 36px;
    }

}