.logo {
    width: 173px;
    height: 33px;
    margin-bottom: 40px;
    cursor: pointer;
}


@media screen and (max-width: 930px){
    .logo {
        margin: 0 auto 32px 24px;
    }   
}

@media screen and (max-width: 420px){

    .logo {
        width: 126px;
        height: 24px;
    }
}